label.bordered-bottom {
  border-bottom: 2px solid #a9a9a9;
  width: 100%;
  transition: 0.2s ease-in-out;
}

label > input {
  border: none;
  outline: none;
}

label.active {
  border-color: var(--color-primary);
}

label.bordered-input {
  background: rgba(58, 61, 66, 0.1);
  width: 100%;
  transition: 0.2s ease-in-out;
  border-bottom: none;
}

label.bordered-input > input {
  border: none;
  outline: none;
  background: transparent;
}

/* label.active {
  border-color: var(--color-primary);
} */

label.borderless {
  background-color: transparent;
  width: 100%;
  transition: 0.2s ease-in-out;
}

label.borderless > input {
  border: none;
  outline: none;
  background-color: transparent;
}

.input-bordered-small {
  height: 65px;
  width: 53px;
  font-size: 30px;
  outline: none;
  border: 2px solid #a9a9a9;
  transition: ease-in-out 0.2s;
  text-align: center;
}

.input-bordered-small::-webkit-input-placeholder {
  position: relative;
  bottom: -10px;
  color: rgba(0, 0, 0, 0.2);
}

.input-bordered-small:-ms-input-placeholder {
  position: relative;
  bottom: -10px;
  color: rgba(0, 0, 0, 0.2);
}

.input-bordered-small::-ms-input-placeholder {
  position: relative;
  bottom: -10px;
  color: rgba(0, 0, 0, 0.2);
}

.input-bordered-small::placeholder {
  position: relative;
  bottom: -10px;
  color: rgba(0, 0, 0, 0.2);
}

@media (min-width: 0) and (max-width: 400px) {
  .input-bordered-small {
    height: 50px;
    width: 100%;
    font-size: 24px;
  }
}

.input-bordered-small:hover,
.input-bordered-small:focus {
  border-color: var(--color-primary);
}

