/* 

    Bootstrap color customization
    Author: Api Rahman

*/

:root {
  --color-primary: #6379f4;
  --color-secondary: #fafcff;
  --color-dark: #3a3d42;
  --color-white: #ffffff;
  --color-danger: #ff5b37;
  --color-gray: #e5e8ed;
}

/* Customize color bootstrap */

/* Text */
.text-primary {
  color: #6379f4 !important;
  color: var(--color-primary) !important;
}

.text-gray {
  color: #e5e8ed !important;
  color: var(--color-gray) !important;
}

.bg-gray {
  background: #e5e8ed !important;
  background: var(--color-gray) !important;
}

.text-secondary {
  color: #fafcff !important;
  color: var(--color-secondary) !important;
}

.text-dark {
  color: #3a3d42 !important;
  color: var(--color-dark) !important;
}

.text-white {
  color: #ffffff !important;
  color: var(--color-white) !important;
}

.text-danger {
  color: #ff5b37 !important;
  color: var(--color-danger) !important;
}

/* Background */

.bg-primary {
  background: #6379f4 !important;
  background: var(--color-primary) !important;
}

.bg-secondary {
  background: #fafcff !important;
  background: var(--color-secondary) !important;
}

.bg-dark {
  background: #3a3d42 !important;
  background: var(--color-dark) !important;
}

.bg-white {
  background: #ffffff !important;
  background: var(--color-white) !important;
}

.bg-danger {
  background: #ff5b37 !important;
  background: var(--color-danger) !important;
}

/* Button background */

.btn-primary,
.btn-primary:hover,
.btn-primary:active,
.btn-primary:focus {
  background: #6379f4 !important;
  background: var(--color-primary) !important;
  border: #6379f4 !important;
  border: var(--color-primary) !important;
}

.btn-secondary,
.btn-secondary:hover,
.btn-secondary:active,
.btn-secondary:focus {
  background: #fafcff !important;
  background: var(--color-secondary) !important;
}

.btn-dark,
.btn-dark:hover,
.btn-dark:active,
.btn-dark:focus {
  background: #3a3d42 !important;
  background: var(--color-dark) !important;
}

.btn-light,
.btn-light:hover,
.btn-light:active,
.btn-light:focus {
  background: #ffffff !important;
  background: var(--color-white) !important;
}

.btn-danger,
.btn-danger:hover,
.btn-danger:active,
.btn-danger:focus {
  background: #ff5b37 !important;
  background: var(--color-danger) !important;
}

/* Button Outline */

.btn-outline-primary {
  outline-color: #6379f4;
  outline-color: var(--color-primary);
}

.btn-outline-secondary {
  outline-color: #fafcff;
  outline-color: var(--color-secondary);
}

.btn-outline-dark {
  outline-color: #3a3d42;
  outline-color: var(--color-dark);
}

.btn-outline-light {
  outline-color: #ffffff;
  outline-color: var(--color-white);
}

.btn-outline-danger {
  outline-color: #ff5b37;
  outline-color: var(--color-danger);
}

.btn-outline-primary:hover,
.btn-outline-primary:focus,
.btn-outline-primary:active {
  background: #6379f4 !important;
  background: var(--color-primary) !important;
  color: white;
}

.btn-outline-secondary:hover,
.btn-outline-secondary:focus,
.btn-outline-secondary:active {
  background: #fafcff !important;
  background: var(--color-secondary) !important;
  color: white;
}

.btn-outline-dark:hover,
.btn-outline-dark:focus,
.btn-outline-dark:active {
  background: #3a3d42 !important;
  background: var(--color-dark) !important;
  color: white;
}

.btn-outline-light:hover,
.btn-outline-light:focus,
.btn-outline-light:active {
  background: unset !important;
  color: white !important;
}

.btn-outline-danger:hover,
.btn-outline-danger:focus,
.btn-outline-danger:active {
  background: #ff5b37 !important;
  background: var(--color-danger) !important;
  color: white;
}

